.loader {
  width: 100px;
  height: 100px;
  font-size: 0;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, 50%);
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
  visibility: hidden;
}

.thumb {
  height: 640px;
  width: 100%;
  border: 2px solid #2a2a2a;
  border-radius: 4px;
  position: relative;
}

.image {
  object-fit: cover;
  width: 100%;
  height: 100%;
  display: block;
}

.catName {
  text-align: center;
  font-size: 25px;
  font-weight: bold;
}

.description, .temperament {
  font-size: 20px;
}

